import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('@/views/HomeView.vue')
  },
  {
    path: '/andrea',
    name: 'andrea',
    component: () => import('@/views/AndreaView.vue')
  },
  {
    path: '/ainhoa',
    name: 'ainhoa',
    component: () => import('@/views/AinhoaView.vue')
  },
  {
    path: '/contacto',
    name: 'contacto',
    component: () => import('@/views/ContactView.vue')
  }
]

const router = new VueRouter({
  mode: "history",
  base: '/portfolio/',
  routes
})

export default router
